import {Address, User} from "../../modules/users/models/User";
import axios from 'axios';
import {UserService} from "../../services/userService";
import {TokenService} from "../../services/tokenService";
import * as alertActions from '../alert/alert.actions';

export const REGISTER_USER_REQUEST : string = 'REGISTER_USER_REQUEST';
export const REGISTER_USER_SUCCESS : string = 'REGISTER_USER_SUCCESS';
export const REGISTER_USER_FAILURE : string = 'REGISTER_USER_FAILURE';

export const LOGIN_USER_REQUEST : string = 'LOGIN_USER_REQUEST';
export const LOGIN_USER_SUCCESS : string = 'LOGIN_USER_SUCCESS';
export const LOGIN_USER_FAILURE : string = 'LOGIN_USER_FAILURE';

export const LOGOUT_USER:string = 'LOGOUT_USER';

export const GET_USER_INFO_REQUEST:string = 'GET_USER_INFO_REQUEST';
export const GET_USER_INFO_SUCCESS:string = 'GET_USER_INFO_SUCCESS';
export const GET_USER_INFO_FAILURE:string = 'GET_USER_INFO_FAILURE';

export const UPDATE_ADDRESS_INFO_REQUEST:string = 'UPDATE_ADDRESS_INFO_REQUEST';
export const UPDATE_ADDRESS_INFO_SUCCESS:string = 'UPDATE_ADDRESS_INFO_SUCCESS';
export const UPDATE_ADDRESS_INFO_FAILURE:string = 'UPDATE_ADDRESS_INFO_FAILURE';

const SERVER_URL = process.env.REACT_APP_SERVER_URL;

export const registerUser = (user : User , history : any) => {
    return async (dispatch:any) => {
        try {
            dispatch({type : REGISTER_USER_REQUEST});
            let dataURL:string = `${SERVER_URL}/api/v1/users/register`;
            let response = await axios.post(dataURL, user);
            console.log(response);
            dispatch({type : REGISTER_USER_SUCCESS, payload : response.data});
            dispatch(alertActions.showAlertMessage('bg-primary' , response.data.msg));
            history.push('/users/login');
        }
        catch (error){
            console.log(error.response);
            let errorArray = error.response.data.errors;
            if(errorArray.length > 0){
                for(let error of errorArray){
                    dispatch(alertActions.showAlertMessage('bg-danger' , error.msg));
                }
            }
            dispatch({type : REGISTER_USER_FAILURE, payload : {error : error.message}});
        }
    };
};

export const loginUser = (user : User , history : any) => {
    return async (dispatch:any) => {
        try {
            dispatch({type : LOGIN_USER_REQUEST});
            let dataURL:string = `${SERVER_URL}/api/v1/users/login`;
            let response = await axios.post(dataURL, user);
            console.log(response);
            dispatch({type : LOGIN_USER_SUCCESS, payload : {token : response.data.token}});
            dispatch(getUserInfo()) // get User Info
            dispatch(alertActions.showAlertMessage('bg-primary' , response.data.msg));
            history.push('/');
        }
        catch (error){
            let errorArray = error.response.data.errors;
            if(errorArray.length > 0){
                for(let error of errorArray){
                    dispatch(alertActions.showAlertMessage('bg-danger' , error.msg));
                }
            }
            dispatch({type : LOGIN_USER_FAILURE, payload : {error : error.message}});
        }
    };
};

export const logOutUser = () => {
    return (dispatch:any) => {
        dispatch({type : LOGOUT_USER});
        dispatch(alertActions.showAlertMessage('bg-primary' , 'Logout is Success!'));
    };
};

// PRIVATE Request
export const getUserInfo = () => {
    return async (dispatch:any) => {
        try {
            let token = UserService.getToken();
            if(token){
                TokenService.setTokenToRequestHeader(token);
                dispatch({type : GET_USER_INFO_REQUEST});
                let dataURL:string = `${SERVER_URL}/api/v1/users/`;
                let response = await axios.get(dataURL);
                console.log(response);
                dispatch({type : GET_USER_INFO_SUCCESS, payload : {user : response.data.user}});
            }
        }
        catch (error){
            dispatch({type : GET_USER_INFO_FAILURE, payload : {error : error.message}});
        }
    };
};

// PRIVATE
export const updateShippingAddress = (address : Address , history : any) => {
    return async (dispatch:any) => {
        try {
            let token = UserService.getToken();
            if(token) {
                TokenService.setTokenToRequestHeader(token);
                dispatch({type : UPDATE_ADDRESS_INFO_REQUEST});
                let dataURL:string = `${SERVER_URL}/api/v1/users/address`;
                let response = await axios.post(dataURL, address);
                dispatch({type : UPDATE_ADDRESS_INFO_SUCCESS, payload : {user : response.data.user}});
                // TODO alert message to be displayed
                history.push('/users/profile');
            }
        }
        catch (error){
            dispatch({type : UPDATE_ADDRESS_INFO_FAILURE, payload : {error : error.message}});
        }
    };
};
