import React, {useEffect, useState} from 'react';
import {useParams, useHistory} from 'react-router-dom';
import * as userReducer from '../../redux/users/user.reducer';
import * as userActions from '../../redux/users/user.actions';
import {useSelector , useDispatch} from "react-redux";
import Spinner from "../layout/Spinner";

interface URLParams{
    userId : string;
}
interface IProps{}
interface IState{
    userKey : userReducer.UserState
}

let EditShippingAddress:React.FC<IProps> = ({}) => {
    let dispatch = useDispatch();
    let history = useHistory();

    let {userId} = useParams<URLParams>();
    let [state , setState] = useState({
        address  : {
            flat : '',
            street : '' ,
            landmark : '',
            city : '' ,
            state : '' ,
            country : '' ,
            pin : '' ,
            phone : ''
        }
    });

    let updateInput = (event : React.ChangeEvent<HTMLInputElement>) => {
        setState({
            address: {
                ...state.address,
                [event.target.name] : event.target.value
            }
        })
    };

    // get User Details from Redux Store
    let userState:userReducer.UserState = useSelector((store: IState) => {
        return store.userKey;
    });

    let {user , loading} = userState;

    let submitUpdateAddress = (event : React.FormEvent<HTMLFormElement>) => {
        event.preventDefault();
        dispatch(userActions.updateShippingAddress(state.address , history));
    };

    useEffect(() => {
        if(user && user.address){
            setState({
                address: {
                    ...state.address,
                    flat: user.address.flat ? user.address.flat : '',
                    street: user.address.street ? user.address.street : '',
                    landmark: user.address.landmark ? user.address.landmark : '',
                    city: user.address.city ? user.address.city : '',
                    state: user.address.state ? user.address.state : '',
                    country: user.address.country ? user.address.country : '',
                    pin: user.address.pin ? user.address.pin : '',
                    phone: user.address.phone ? user.address.phone : ''
                }
            })
        }
    }, []);

    return(
        <React.Fragment>
            <section className="mt-3">
                <div className="container">
                    <div className="row">
                        <div className="col">
                            <p className="h3 text-success">EditShippingAddress</p>
                            <p>Lorem ipsum dolor sit amet, consectetur adipisicing elit. At autem blanditiis commodi consequatur corporis distinctio dolores doloribus enim, error harum, labore natus necessitatibus neque quae, rem ullam unde vel voluptas?</p>
                        </div>
                    </div>
                </div>
            </section>
            {
                loading ? <Spinner/> :
                    <React.Fragment>
                        {
                            user && Object.keys(user).length > 0 &&
                            <section className="mt-3">
                                <div className="container">
                                    <div className="row">
                                        <div className="col-md-4">
                                            <form onSubmit={submitUpdateAddress}>
                                                <div className="mb-2">
                                                    <input
                                                        required
                                                        name="flat"
                                                        value={state.address.flat}
                                                        onChange={updateInput}
                                                        type="text" className="form-control" placeholder="Flat"/>
                                                </div>
                                                <div className="mb-2">
                                                    <input
                                                        required
                                                        name="street"
                                                        value={state.address.street}
                                                        onChange={updateInput}
                                                        type="text" className="form-control" placeholder="Street"/>
                                                </div>
                                                <div className="mb-2">
                                                    <input
                                                        required
                                                        name="landmark"
                                                        value={state.address.landmark}
                                                        onChange={updateInput}
                                                        type="text" className="form-control" placeholder="Landmark"/>
                                                </div>
                                                <div className="mb-2">
                                                    <input
                                                        required
                                                        name="city"
                                                        value={state.address.city}
                                                        onChange={updateInput}
                                                        type="text" className="form-control" placeholder="City"/>
                                                </div>
                                                <div className="mb-2">
                                                    <input
                                                        required
                                                        name="state"
                                                        value={state.address.state}
                                                        onChange={updateInput}
                                                        type="text" className="form-control" placeholder="State"/>
                                                </div>
                                                <div className="mb-2">
                                                    <input
                                                        required
                                                        name="country"
                                                        value={state.address.country}
                                                        onChange={updateInput}
                                                        type="text" className="form-control" placeholder="Country"/>
                                                </div>
                                                <div className="mb-2">
                                                    <input
                                                        required
                                                        name="pin"
                                                        value={state.address.pin}
                                                        onChange={updateInput}
                                                        type="text" className="form-control" placeholder="Pin"/>
                                                </div>
                                                <div className="mb-2">
                                                    <input
                                                        required
                                                        name="phone"
                                                        value={state.address.phone}
                                                        onChange={updateInput}
                                                        type="text" className="form-control" placeholder="Mobile"/>
                                                </div>
                                                <div className="mb-2">
                                                    <input type="submit" className="btn btn-success" value="Update"/>
                                                </div>
                                            </form>
                                        </div>
                                    </div>
                                </div>
                            </section>
                        }
                    </React.Fragment>
            }
        </React.Fragment>
    )
}
export default EditShippingAddress;