import React, {useState} from 'react';
import {Product} from "./models/Product";
import {useHistory} from 'react-router-dom';
import * as productActions from '../../redux/products/product.actions';
import * as productReducer from '../../redux/products/product.reducer';
import {useDispatch , useSelector} from "react-redux";
import * as userReducer from '../../redux/users/user.reducer';
import {ReactComponent} from "*.svg";

interface IProps{}
interface IState{
    product : Product
}
interface IUSerState{
    userKey : userReducer.UserState
}

let ProductUpload:React.FC<IProps> = ({}) => {
    let history = useHistory();
    let dispatch = useDispatch();

    // get user details
    let userState:userReducer.UserState = useSelector((store : IUSerState) => {
        return store.userKey;
    });

    let {isAdmin} = userState.user;

    let [state , setState] = useState<IState>({
        product : {
            name : '',
            brand : '',
            image : '',
            price : 0,
            qty : 0,
            category : '',
            description : '',
            usage : ''
        }
    });

    let updateInput = (event : React.ChangeEvent<HTMLInputElement | HTMLSelectElement | HTMLTextAreaElement>) => {
        setState({
            product : {
                ...state.product,
                [event.target.name] : event.target.value
            }
        });
    };

    let updateImage = async (event:React.ChangeEvent<HTMLInputElement | any>) => {
        let imageFile:Blob = event.target.files[0];
        let base64Image:string | ArrayBuffer = await convertBase64String(imageFile);
        setState({
            ...state,
            product : {
                ...state.product,
                image : base64Image.toString()
            }
        });
    };

    let convertBase64String = (imageFile:Blob):Promise<string | ArrayBuffer> => {
        return new Promise((resolve, reject) => {
            let fileReader = new FileReader();
            fileReader.readAsDataURL(imageFile);
            fileReader.addEventListener('load', () => {
                if(fileReader.result){
                    resolve(fileReader.result);
                }
                else {
                    reject('Error Occurred');
                }
            })
        });
    };

    let submitUploadProduct = (event : React.FormEvent<HTMLFormElement>) => {
        event.preventDefault();
        dispatch(productActions.uploadProduct(state.product, history));
    };

    return(
        <React.Fragment>
           {/* <pre>{JSON.stringify(state.product)}</pre>*/}
            <section className="mt-3">
                <div className="container">
                    <div className="row">
                        <div className="col">
                            <p className="h3 text-success">Upload Products Here</p>
                            <p>Lorem ipsum dolor sit amet, consectetur adipisicing elit. At autem blanditiis commodi consequatur corporis distinctio dolores doloribus enim, error harum, labore natus necessitatibus neque quae, rem ullam unde vel voluptas?</p>
                        </div>
                    </div>
                </div>
            </section>
            {
                !isAdmin ? <React.Fragment>
                   <div className="container mt-3">
                       <div className="row">
                           <div className="col">
                               <p className="h5 text-danger">You are not authorized to Upload Images</p>
                               <small className="text-danger">Please contact the Application DBA, to grant Admin access</small>
                           </div>
                       </div>
                   </div>
                </React.Fragment> :
                    <React.Fragment>
                        <section className="mt-3">
                            <div className="container">
                                <div className="row">
                                    <div className="col-md-6">
                                        <form onSubmit={submitUploadProduct}>
                                            <div className="mb-2">
                                                <input
                                                    required
                                                    name={'name'}
                                                    value={state.product.name}
                                                    onChange={updateInput}
                                                    type="text" className="form-control" placeholder="Name"/>
                                            </div>
                                            <div className="mb-2">
                                                <input
                                                    required
                                                    name={'brand'}
                                                    value={state.product.brand}
                                                    onChange={updateInput}
                                                    type="text" className="form-control" placeholder="Brand"/>
                                            </div>
                                            <div className="mb-2">
                                                <input
                                                    required
                                                    name={'image'}
                                                    onChange={updateImage}
                                                    className="form-control" type="file" id="formFile"/>
                                                <div>
                                                    {
                                                        state.product && state.product.image && state.product.image?.length > 0 ?
                                                            <img src={state.product.image} alt={''} width={'40'} height={'50'}/> : null
                                                    }
                                                </div>
                                            </div>
                                            <div className="mb-2">
                                                <input
                                                    required
                                                    name={'price'}
                                                    value={state.product.price}
                                                    onChange={updateInput}
                                                    type="number" className="form-control" placeholder="Price"/>
                                            </div>
                                            <div className="mb-2">
                                                <input
                                                    required
                                                    name={'qty'}
                                                    value={state.product.qty}
                                                    onChange={updateInput}
                                                    type="number" className="form-control" placeholder="Qty"/>
                                            </div>
                                            <div className="mb-2">
                                                <select
                                                    required
                                                    name={'category'}
                                                    value={state.product.category}
                                                    onChange={updateInput}
                                                    className="form-control">
                                                    <option value="">Select a Category</option>
                                                    <option value="MEN_FASHION">Mens Fashion</option>
                                                    <option value="WOMEN_FASHION">Women's Fashion</option>
                                                    <option value="KIDS_FASHION">Kids Fashion</option>
                                                </select>
                                            </div>
                                            <div className="mb-2">
                                    <textarea
                                        required
                                        name={'description'}
                                        value={state.product.description}
                                        onChange={updateInput}
                                        rows={4} className="form-control" placeholder="Description"/>
                                            </div>
                                            <div className="mb-2">
                                    <textarea
                                        required
                                        name={'usage'}
                                        value={state.product.usage}
                                        onChange={updateInput}
                                        rows={4} className="form-control" placeholder="Usage"/>
                                            </div>
                                            <div className="mb-2">
                                                <input type="submit" className="btn btn-success btn-sm" value="Upload"/>
                                            </div>
                                        </form>
                                    </div>
                                </div>
                            </div>
                        </section>
                    </React.Fragment>
            }
        </React.Fragment>
    )
}
export default ProductUpload;