import * as alertActions from './alert.actions';

export interface AlertType{
    id : string;
    color : string;
    msg : string;
}

export interface AlertState {
    alerts : AlertType[]
}

let initialState:AlertState = {
    alerts : [] as AlertType[]
}

export const reducer = (state= initialState , action:any):AlertState => {
    switch(action.type){
        case alertActions.SHOW_ALERT_MESSAGE:
            return {
                ...state,
                alerts : [...state.alerts , {
                    id : action.payload.id,
                    color : action.payload.color,
                    msg : action.payload.msg,
                }]
            };
        case alertActions.HIDE_ALERT_MESSAGE:
           let remAlerts:AlertType[] = state.alerts.filter(alert => alert.id !== action.payload.id);
           return {
               alerts : [...remAlerts]
           };
        default : return state;
    }
};













