import {Product} from "../../modules/products/models/Product";
import * as alertActions from '../alert/alert.actions';
import {UserService} from "../../services/userService";
import {TokenService} from "../../services/tokenService";
import axios from "axios";

const SERVER_URL = process.env.REACT_APP_SERVER_URL;

export const ADD_TO_CART_SUCCESS:string = 'ADD_TO_CART_SUCCESS';
export const ADD_TO_CART_FAILURE:string = 'ADD_TO_CART_FAILURE';

export const INCR_PRODUCT_QTY:string = 'INCR_PRODUCT_QTY';
export const DECR_PRODUCT_QTY:string = 'DECR_PRODUCT_QTY';
export const DELETE_CART_ITEM:string = 'DELETE_CART_ITEM';

export const MAKE_STRIPE_PAYMENT_REQUEST : string = 'MAKE_STRIPE_PAYMENT_REQUEST';
export const MAKE_STRIPE_PAYMENT_SUCCESS : string = 'MAKE_STRIPE_PAYMENT_SUCCESS';
export const MAKE_STRIPE_PAYMENT_FAILURE : string = 'MAKE_STRIPE_PAYMENT_FAILURE';

export const PLACE_ORDER_REQUEST : string = 'PLACE_ORDER_REQUEST';
export const PLACE_ORDER_SUCCESS: string = 'PLACE_ORDER_SUCCESS';
export const PLACE_ORDER_FAILURE: string = 'PLACE_ORDER_FAILURE';

export const GET_ALL_ORDERS_REQUEST:string = 'GET_ALL_ORDERS_REQUEST';
export const GET_ALL_ORDERS_SUCCESS:string = 'GET_ALL_ORDERS_SUCCESS';
export const GET_ALL_ORDERS_FAILURE:string = 'GET_ALL_ORDERS_FAILURE';

export const CLEAR_CART:string = 'CLEAR_CART';

export const addToCart = (product : Product) => {
    return (dispatch:any) => {
        try {
            dispatch({type : ADD_TO_CART_SUCCESS, payload : {product : product}});
            dispatch(alertActions.showAlertMessage('bg-primary' , 'Item is added to Cart'));
        }
        catch (error){
            dispatch({type : ADD_TO_CART_FAILURE, payload : {error : error}});
        }
    };
};

export const incrProductQty = (productId : string) => {
    return (dispatch:any) => {
        try {
            dispatch({type : INCR_PRODUCT_QTY, payload : {productId : productId}});
        }
        catch (error){
            console.error(error);
        }
    };
};

export const decrProductQty = (productId : string) => {
    return (dispatch:any) => {
        try {
            dispatch({type : DECR_PRODUCT_QTY, payload : {productId : productId}});
        }
        catch (error){
            console.error(error);
        }
    };
};

export const deleteCartItem = (productId : string) => {
    return (dispatch:any) => {
        try {
            dispatch({type : DELETE_CART_ITEM, payload : {productId : productId}});
            dispatch(alertActions.showAlertMessage('bg-warning' , 'Item is removed'));
        }
        catch (error){
            console.error(error);
        }
    };
};

export const clearCart = () => {
    return (dispatch:any) => {
        try {
            dispatch({type : CLEAR_CART});
        }
        catch (error){
            console.error(error);
        }
    };
};

// PRIVATE Request
export const makeStripePayment = (body:any, order : any, history:any) => {
    return async (dispatch :any) => {
        try {
            dispatch({type : MAKE_STRIPE_PAYMENT_REQUEST});
            let token = UserService.getToken();
            if(token) {
                TokenService.setTokenToRequestHeader(token);
                TokenService.setStripeKey();
                let dataURL:string = `${SERVER_URL}/api/v1/payments/checkout`;
                let response = await axios.post(dataURL, body);
                dispatch({
                    type : MAKE_STRIPE_PAYMENT_SUCCESS,
                    payload : response.data
                });
                // place an order
                dispatch(placeOrder(order));
                dispatch(alertActions.showAlertMessage('bg-primary', 'Payment is Success'));
                history.push('/');
            }
        }
        catch (error){
            dispatch({
                type : MAKE_STRIPE_PAYMENT_FAILURE,
                payload : {error : error}
            });
        }
    };
};

// PRIVATE URL
export const placeOrder = (order : any) => {
    return async (dispatch: any) => {
        try {
            dispatch({type : PLACE_ORDER_REQUEST});
            let token = UserService.getToken();
            if(token) {
                TokenService.setTokenToRequestHeader(token);
                let dataURL : string = `${SERVER_URL}/api/v1/orders/place`;
                let response = await axios.post(dataURL, order);
                dispatch({type: PLACE_ORDER_SUCCESS , payload : response.data});
                dispatch(clearCart()); // clear the cart
                dispatch(alertActions.showAlertMessage('bg-success', response.data.msg));
            }
        }
        catch (error){
            dispatch({type: PLACE_ORDER_FAILURE , payload : {error : error}});
        }
    };
};

// PRIVATE URL
export const getAllOrders = () => {
    return async (dispatch: any) => {
        try {
            dispatch({type : GET_ALL_ORDERS_REQUEST});
            let token = UserService.getToken();
            if(token) {
                TokenService.setTokenToRequestHeader(token);
                let dataURL : string = `${SERVER_URL}/api/v1/orders/`;
                let response = await axios.get(dataURL);
                console.log(response.data);
                dispatch({type: GET_ALL_ORDERS_SUCCESS , payload : {orders : response.data.orders}});
            }
        }
        catch (error){
            console.log(error.response);
            dispatch({type: GET_ALL_ORDERS_FAILURE , payload : {error : error}});
        }
    };
};
