import { v4 as uuidv4 } from 'uuid';

export const SHOW_ALERT_MESSAGE:string = 'SHOW_ALERT_MESSAGE';
export const HIDE_ALERT_MESSAGE:string = 'HIDE_ALERT_MESSAGE';

export const showAlertMessage = (color:string , msg: string) => {
    let id = uuidv4();
    return (dispatch:any) => {
        try {
            dispatch({type : SHOW_ALERT_MESSAGE, payload : {
                id : id,
                color : color,
                msg : msg
            }});
            setTimeout(() => {
                dispatch(hideAlertMessage(id));
            }, 3000); // close the alert after 3 seconds automatically
        }
        catch (error){
            console.error(error);
        }
    };
};

export const hideAlertMessage = (id : string) => {
    return (dispatch:any) => {
        try {
            dispatch({type : HIDE_ALERT_MESSAGE, payload : {
                    id : id
                }});
        }
        catch (error){
            console.error(error);
        }
    };
};