import React, {useEffect, useState} from 'react';
import {useParams} from 'react-router-dom';
import * as productActions from '../../redux/products/product.actions';
import * as productReducer from '../../redux/products/product.reducer';
import {useSelector , useDispatch} from 'react-redux';
import Spinner from "../layout/Spinner";
import * as orderActions from '../../redux/orders/order.actions';

interface UrlParam{
    productId : string;
}
interface IProps{}
interface IState{
    productKey : productReducer.ProductState
}

let ProductDetails:React.FC<IProps> = ({}) => {
    let [state , setState] = useState<{qty : number}>({
        qty : 1
    });

    let dispatch = useDispatch();
    let {productId} = useParams<UrlParam>();

    // get the product state from Redux Store
    let productState:productReducer.ProductState = useSelector((store:  IState) => {
        return store.productKey;
    });

    let {selectedProduct , loading , errorMessage} = productState;

    useEffect(() => {
        dispatch(productActions.getProduct(productId));
    }, [productId]);

    let clickAddCart = () => {
        dispatch(orderActions.addToCart({
            ...selectedProduct,
            qty :state.qty
        }));
    };

    return(
        <React.Fragment>
            <section className="mt-3">
                <div className="container">
                    <div className="row">
                        <div className="col">
                            <p className="h3 text-success">Selected Product</p>
                            <p>Lorem ipsum dolor sit amet, consectetur adipisicing elit. At autem blanditiis commodi consequatur corporis distinctio dolores doloribus enim, error harum, labore natus necessitatibus neque quae, rem ullam unde vel voluptas?</p>
                        </div>
                    </div>
                </div>
            </section>
            {
                loading ? <Spinner/> : <React.Fragment>
                    {
                        Object.keys(selectedProduct).length > 0 &&
                        <section className="mt-3">
                            <div className="container">
                                <div className="row">
                                    <div className="col-md-4 text-center">
                                        <img src={selectedProduct.image} alt={''} className="shadow-lg"/>
                                    </div>
                                    <div className="col-md-8">
                                        <p className="h4">NAME : <span className="fw-bold">{selectedProduct.name}</span></p>
                                        <p className="h4">Brand : <span className="fw-bold">{selectedProduct.brand}</span></p>
                                        <p className="h4">Price : <span className="fw-bold">&#8377;{selectedProduct.price}</span></p>
                                            <div className="row">
                                                <div className="col-md-3">
                                                    <select
                                                        required
                                                        value={state.qty}
                                                        onChange={(event) => {setState({qty : Number(event.target.value)})}}
                                                        className="form-control">
                                                        <option value="">Select Qty</option>
                                                        <option value="1">1</option>
                                                        <option value="2">2</option>
                                                        <option value="3">3</option>
                                                        <option value="4">4</option>
                                                        <option value="5">5</option>
                                                    </select>
                                                    <button className="btn btn-success btn-sm" onClick={clickAddCart}>Add To Cart</button>
                                                </div>
                                            </div>     
                                        <p>Description : {selectedProduct.description}</p>
                                        <p>Usage : {selectedProduct.usage}</p>
                                    </div>
                                </div>
                            </div>
                        </section>
                    }
                </React.Fragment>
            }
        </React.Fragment>
    )
}
export default ProductDetails;