import React from 'react';
import {NavLink} from "react-router-dom";
import * as orderReducer from '../../redux/orders/order.reducer';
import {useSelector} from 'react-redux';
import {UserService} from "../../services/userService";
import * as userActions from '../../redux/users/user.actions';
import * as userReducer from '../../redux/users/user.reducer';
import {useDispatch} from "react-redux";

interface IProps{}
interface IOrderState{
    orderKey : orderReducer.OrderState
}
interface IUserState{
    userKey : userReducer.UserState
}

let NavBar:React.FC<IProps> = ({}) => {
    let dispatch = useDispatch();

    let orderState:orderReducer.OrderState = useSelector((store:  IOrderState) => {
        return store.orderKey;
    });

    let userState:userReducer.UserState = useSelector((store:  IUserState) => {
        return store.userKey;
    });

    let { isAuthenticated , user} = userState;

    let {cartItems , errorMessage} = orderState;

    let isLoggedIn = () => {
        return !!UserService.getToken();
    };

    let clickLogOut = () => {
        dispatch(userActions.logOutUser());
    };

    return(
        <React.Fragment>
           <nav className="navbar navbar-dark bg-success navbar-expand-sm">
               <div className="container">
                   <NavLink to={'/'} className="navbar-brand">Online Shopping</NavLink>
                  <div className="collapse navbar-collapse">
                      <ul className="navbar-nav me-auto">
                          <li className="nav-item">
                              <NavLink to={'/fashion/men'} className="nav-link">Mens Fashion</NavLink>
                          </li>
                          <li className="nav-item">
                              <NavLink to={'/fashion/women'} className="nav-link">Women Fashion</NavLink>
                          </li>
                          <li className="nav-item">
                              <NavLink to={'/fashion/kids'} className="nav-link">Kids Fashion</NavLink>
                          </li>
                          {
                              isLoggedIn() &&
                              <li className="nav-item">
                                  <NavLink to={'/products/upload'} className="nav-link">Upload</NavLink>
                              </li>
                          }
                          <li className="nav-item">
                              <NavLink to={'/orders/cart'} className="nav-link">
                                  <i className="fa fa-shopping-cart"/>
                                  <span className="badge bg-danger">{cartItems.length}</span>
                              </NavLink>
                          </li>
                      </ul>
                      <div className="d-flex">
                          <ul className="navbar-nav">
                              {
                                  isLoggedIn() && isAuthenticated ?
                                      <React.Fragment>
                                          {
                                              user && Object.keys(user).length > 0 &&
                                              <li className="nav-item">
                                                  <NavLink to={'/users/profile'} className="nav-link">
                                                      <img src={user.avatar} alt={''} width={20} height={20} className="rounded-circle"/> {user.name}</NavLink>
                                              </li>
                                          }
                                      <li className="nav-item">
                                          <NavLink onClick={clickLogOut} to={'/'} className="nav-link">LogOut</NavLink>
                                      </li>
                                    </React.Fragment>
                                      :
                                    <React.Fragment>
                                      <li className="nav-item">
                                          <NavLink to={'/users/login'} className="nav-link">Login</NavLink>
                                      </li>
                                      <li className="nav-item">
                                          <NavLink to={'/users/register'} className="nav-link">Register</NavLink>
                                      </li>
                                  </React.Fragment>
                              }
                          </ul>
                      </div>
                  </div>
               </div>
           </nav>
        </React.Fragment>
    )
};
export default NavBar;