import React from 'react';
import * as alertReducer from '../../redux/alert/alert.reducer';
import {useSelector} from "react-redux";


interface IProps{}
interface IState{
    alertKey : alertReducer.AlertState
}

let Alert:React.FC<IProps> = ({}) => {

    // get alert messages from Redux Store
    let alertState:alertReducer.AlertState = useSelector((store : IState) => {
        return store.alertKey;
    });

    let {alerts} = alertState;

    return(
        <React.Fragment>
            {
                alerts && alerts.length > 0 &&
                    alerts.map(alert => {
                        return(
                            <div key={alert.id} className={`alert text-white d-flex align-items-center alert-dismissible animate__animated animate__slideInUp fixed-bottom ${alert.color}`} role='alert'>
                                <i className="fa fa-check-circle"/>
                                <div>
                                    &nbsp; {alert.msg}
                                </div>
                                <button type="button" className="btn-close text-white" data-bs-dismiss="alert" aria-label="Close"/>
                            </div>
                        )
                    })
            }
        </React.Fragment>
    )
};
export default Alert;