import React, {useEffect} from 'react';
import {useDispatch, useSelector} from "react-redux";
import * as productReducer from "../../redux/products/product.reducer";
import * as productActions from "../../redux/products/product.actions";
import * as orderActions from '../../redux/orders/order.actions';
import Spinner from "../layout/Spinner";
import {NavLink} from "react-router-dom";
import {Product} from "./models/Product";

interface IProps{}
interface IState{
    productKey : productReducer.ProductState
}

let WomenFashion:React.FC<IProps> = ({}) => {
    let dispatch = useDispatch();
    const category:string = 'WOMEN_FASHION'

    // get products data from REDUX store
    let productState:productReducer.ProductState = useSelector((store:IState) => {
        return store.productKey;
    });

    let {products , loading, errorMessage} = productState;

    useEffect(() => {
        dispatch(productActions.getProducts(category));
    }, []);

    let clickAddToCart = (product : Product) => {
        dispatch(orderActions.addToCart({
            ...product,
            qty : 1
        }));
    };

    return(
        <React.Fragment>
            <section className="mt-3">
                <div className="container">
                    <div className="row">
                        <div className="col">
                            <p className="h3 text-success">Women's Fashion</p>
                            <p>Lorem ipsum dolor sit amet, consectetur adipisicing elit. At autem blanditiis commodi consequatur corporis distinctio dolores doloribus enim, error harum, labore natus necessitatibus neque quae, rem ullam unde vel voluptas?</p>
                        </div>
                    </div>
                </div>
            </section>
            {
                loading ? <Spinner/> : <React.Fragment>
                    {
                        products.length > 0 ? <React.Fragment>
                            <section className="">
                                <div className="container">
                                    <div className="row">
                                        {
                                            products.map(product => {
                                                return (
                                                    <div className="col-md-3" key={product._id}>
                                                        <div className="card shadow-lg mt-3">
                                                            <NavLink to={`/products/${product._id}`}>
                                                                <img src={product.image} alt={''} width={200} height={300} className="m-auto d-block"/>
                                                            </NavLink>
                                                            <div className="card-body">
                                                                <ul className="list-group">
                                                                    <li className="list-group-item">
                                                                        NAME : <span className="fw-bold">{product.name}</span>
                                                                    </li>
                                                                    <li className="list-group-item">
                                                                        Brand : <span className="fw-bold">{product.brand}</span>
                                                                    </li>
                                                                    <li className="list-group-item">
                                                                        Price : <span className="fw-bold">&#8377; {product.price}</span>
                                                                    </li>
                                                                </ul>
                                                                <button onClick={clickAddToCart.bind(this, product)} className="btn btn-success btn-sm">Add to Cart</button>
                                                            </div>
                                                        </div>
                                                    </div>
                                                )
                                            })
                                        }
                                    </div>
                                </div>
                            </section>
                        </React.Fragment> : null
                    }
                </React.Fragment>
            }
        </React.Fragment>
    )
};
export default WomenFashion;